let timer = '';
import http from '@/api/http.js'
import {checkPhone,checkPwd,checkCode} from "@/common/common.js"
export default {
	data() {
		return {
			sendMsg:"获取验证码",
			isCanSendCode:true,
			phone:"",
			password1:"",
			password2:"",
			code:""
		};
	},
	onLoad() {
		this.phone = this.$util.getStorage('phone')
		console.log(this.$util.getStorage('phone'))
	},
	methods: {
		setCodeInterval () {
			//设置验证码重新发送定时器
		    let time = 60;
		    clearInterval(timer);
		    timer = setInterval(() => {
		        time--;
		        this.isCanSendCode=false;
		        this.sendMsg=time + "s"
		        if (time <= 0) {
		            this.isCanSendCode=true;
		            this.sendMsg="重新获取";
		            clearInterval(timer);
		        }
		    }, 1000)
		},
		clearCodeInterval(){
		    clearInterval(timer);
		    this.codemsg="获取验证码";
		},
		sendCode () {
			let data = {
				phone:this.phone
			}
			http('/api/Login/send_code','post',data).then(res=>{
				if(res.data.status == '200'){
					uni.showToast({
						title:res.data.message
					})
				}
				if(res.data.status == '400'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				console.log(res)
			})
			//发送验证码
		    if (!checkPhone(this.phone)) {
		        return
		    }
		    this.setCodeInterval();
			
			/*
			* 发送验证码逻辑
			*/
		},
		submit() {
			if(this.password1 == ''|| this.password2 == '' || this.phone == '' || this.code == ''){
				this.$util.showToast('不能为空')
				return;
			}
			if(!checkPhone(this.phone)){
				return;
			};
			if(!checkPwd(this.password1)){
				return;
			};
			if(!checkCode(this.code)){
				this.$util.showToast('验证码错误')
				return;
			};
			if(this.password1 != this.password2){
				this.$util.showToast('两次密码不一致')
				
				return;
			};
			let data = {
				token:uni.getStorageSync('token'),
				code:this.code,
				payment_password:this.password1,
				repassword:this.password2
			}
			http('/api/user/saveUserPaymentPassword','post',data).then(res=>{
				if(res.data.status == '200'){
					
					this.$util.showToast('设置成功')
					setTimeout(()=>{
						uni.switchTab({
							url:'/pages/mine/mine'
						})
					},1500)
					
				}
				if(res.data.status == '400'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
					return;
				}
				console.log(res)
			})
			// uni.reLaunch({
			// 	url:'/pages/index/logon/logon'
			// })
		}
	}
}
